body {
  padding:0px;
  margin:0px;overflow:hidden;
  font-family: 'RalewayMedium';
}
:focus {
    outline: 0;
}

/* SCOLLBARS */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}
::-webkit-scrollbar-thumb {
  background: #1eac76;
  border: 0px none #ffffff;
  border-radius: 0px;
}
::-webkit-scrollbar-thumb:hover {
  background: #1eac76;
}
::-webkit-scrollbar-thumb:active {
  background: #16e295;
}
::-webkit-scrollbar-track {
  background: #333333;
  border: 0px none #ffffff;
  border-radius: 50px;
}
::-webkit-scrollbar-track:hover {
  background: #333333;
}
::-webkit-scrollbar-track:active {
  background: #333333;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
/* /SCROLLBARS */

.leaflet-container.leaflet-touch-drag.leaflet-touch-drag {
    -webkit-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.leaflet-pane, svg {
  -webkit-user-select: none;
}

/* MISCELANEOUS */
.pointer {
  cursor: pointer;
}
.hidden {
  display: none;
}
.clr {
    clear:both;
}
.gray {
    opacity: 0.7;
}
.small {
    font-size: 0.8em;
}
.single {
  height: 10px;
}
.double {
  height: 20px;
}
.triple {
  height: 30px;
}
.quadro {
  height: 40px;
}
.leaflet-touch .leaflet-control-layers, .leaflet-touch .leaflet-bar {
    border:none;
}
.ctrl-search {
  position: absolute;
    z-index: 1000;
    left: 76px;
    top: 8px;
    border-radius: 40px;
    background: #666  no-repeat -584px -8px;
    border-radius: 100% !important;
    font-size: 16px;
    width: 32px;
    height: 32px;
    box-sizing: border-box;
    line-height: 24px;
    box-shadow: #444 0 0 0 4px;
    color: white;
    padding: 6px 0 0 0; text-align:center;
    transition: background-color 250ms;cursor:pointer;
}
.ctrl-panzoom {
  position: absolute;
    z-index: 1000;
    left: 16px;
    top: 108px;
    border-radius: 40px;
    background: #666 url(ea7ec57f94d6646f09c436220c9b913e.png) no-repeat -584px -8px;
    border-radius: 100% !important;
    font-size: 28px;
    width: 32px;
    height: 32px;
    box-sizing: border-box;
    line-height: 24px;
    box-shadow: #444 0 0 0 4px;
    color: white;
    transition: background-color 250ms;cursor:pointer;
}

.ctrl-panzoom::before {
    content: ' ';
    position: absolute;
    top: -8px;
    left: 8px;
    width: 18px;
    height: 8px;
    background: #444;
}

