a.leaflet-control-zoom-in,a.leaflet-control-zoom-in:hover {
  border-radius: 100% !important;
  font-size: 28px !important;
  width: 48px !important;
  height: 48px !important;
  box-sizing: border-box;
  line-height: 48px !important;
  box-shadow: #444 0 0 0 4px;
  background: #666;
  color: white;
  border-bottom: 0px;
  transition: background 250ms;
}

a.leaflet-control-zoom-out,a.leaflet-control-zoom-out:hover {
    padding-top: 3px;
    background: #666;
    border-radius: 100% !important;
    font-size: 28px !important;
    width: 33px !important;
    height: 33px !important;
    box-sizing: border-box;
    line-height: 24px !important;
    box-shadow: #444 0 0 0 4px;
    position:relative;
    left:6px;
    top:8px;
    color:white;
    transition: background 250ms;
}
a.leaflet-control-zoom-in.leaflet-disabled,a.leaflet-control-zoom-out.leaflet-disabled{
      background: #4e4e4e;
    color: #666;
}
a.leaflet-control-zoom-out:hover, a.leaflet-control-zoom-in:hover, .ctrl-panzoom:hover {
  background-color: #3385ff;
}
a.leaflet-control-zoom-out:before {
  content: ' ';
  position:absolute;top: -8px;left:8px;
  width: 18px;height:6px;background:#444;
}

