.point_live {
    position: absolute;
    white-space: nowrap;
    width: auto !important;
    /* text-align: right; */
    /* margin-left: 15px !important; */
    /* margin-top: -9px !important; */
    background: #444;
    /* line-height: 20px; */
    height: 15px !important;
    padding: 5px 10px 7px;
    font-size: 14px;
    line-height: 14px;
    border-radius: 2px;
    color: white;
    /* font-weight: bold; */
    font-family: 'RalewayBold';
    position: absolute;
    left: -3px;
    top: -8px;
}
.point_live.inverse{
    left: auto;
    right: -3px;
}
.point_live.inverse .point_drop {
      right: auto;
    left: -25px;
    border-radius: 4px 0 0 4px;
}
.point_label {
    display: inline-block;
    opacity: 0;
    color: red;
    padding: 2px 10px 0px;

}
.point_drop {
    width: 27px;
    height: 100%;
    position: absolute;
    right: -26px;
    top: 0px;
    background: #56a7a5;
    border-radius: 0 4px 4px 0;
    padding: 7px 8px;
    box-sizing: border-box;
}
.point_drop::before {
  content: 'X';
}
.point_live input {
  background: none;
  border: none;
  outline: none;
  font-size: inherit;
  color: inherit;
  line-height: inherit;
  font-weight: inherit;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  padding: 0 10px 0 20px;
  color: white;font-family:inherit;
}

.point_editor {
    position: absolute;
    top: -10px;
    left: 0px;
    background: #444;
    box-sizing: border-box;
    padding: 10px;
    z-index: 2;
    border-radius: 4px;
    display:none;
}
.point_colors {
    position: absolute;
    bottom: -15px;
    left: 0px;
    width: 150px;
    height: 16px;
    background: #333;
    border-radius: 0px 0px 4px 4px;
}
.point_colors div {
  width: 25px;
  height:16px;
  background:red;float:left;
}
div.col-1 {
  background-color: #444;
}
div.col-2 {
