}
div.col-5 {
  background-color: cyan;
}
div.col-6 {
  background-color: magenta;
}
.point_colors div:first-child {
  border-radius: 0 0 0 4px;
}
.point_colors div:last-child {
  border-radius: 0 0 4px 0;
}
.mark.editor .point_editor {
  display: block;
