.btn {
  width:48px;height:48px;float:left;
  background: url(ea7ec57f94d6646f09c436220c9b913e.png) no-repeat;
  cursor: pointer;
  transform: scale(1);
  transition: transform 100ms;
}
.btn:active {
  transform: scale(1.1);
}
.btn span, .btn.active:hover span, .bar_text span {
    user-select: none;
    position: absolute;
    bottom: 58px;
    color: white;
    background: #111;
    padding: 10px;
    width: auto;
    display: inline-block;
    /* min-width: 200px; */
    z-index: 0;
    left: 0px;
    border-radius: 5px;
    transform: scaleY(0);
    transition: transform 250ms;
    transform-origin: 100% 110%;
    /* color: #fff; */
    font-family: 'RalewayMedium';
    font-size: 13px;
    white-space: nowrap;font-weight: normal;text-transform: none;
}
.btn span::before, .bar_text span::before {
    content: ' ';
    position:absolute;bottom:-14px;left:10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 15px 15px 0 15px;
    border-color: #111 transparent transparent transparent;
}
.btn span b, b.key {
    color: white;
    float: none;
    background: #fff;
    color: #111;
    font-size: 12px;
    padding: 2px 6px;
    border-radius: 2px;
    /* border: 3px #ccc solid; */
    /* border-width: 0px 1px 3px 1px; */
