/* FINE UPLOADER */
.qq-upload-button-selector.qq-upload-button {
    width: 48px;
    height: 48px;
    background: #17bf6d;
    border-radius: 24px;
    position: absolute !important;
    right: 10px;
    bottom: 10px;
    font-size: 32px;
    font-weight: normal;
    box-sizing: border-box;
    padding: 10px 0 0 0px;
    text-align: center;
    display: none;
    background: url(ea7ec57f94d6646f09c436220c9b913e.png) #17bf6d -480px -48px;
    transition: background-color 500ms, background-position 500ms;
}
.upload_success .qq-upload-button-selector.qq-upload-button {
    background-position: -528px -96px;
    background-color: #74de48;
}
.upload_error .qq-upload-button-selector.qq-upload-button {
    background-color: #ff3344;
    background-position: -528px -48px;
}
.upload_started .qq-upload-button-selector.qq-upload-button {
  background-position: -480px -96px;
  animation: rotapulse infinite 1s linear;
}
.can_edit .qq-upload-button-selector.qq-upload-button {
  display: block;
}
.qq-upload-button-selector.qq-upload-button:hover {
  animation: bounce 0.5s alternate infinite;
}
@keyframes rotapulse {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
@keyframes bounce {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.qq-upload-list-selector {
  display:none;
}

.qq-upload-drop-area {
  position:absolute;left:0px;top:0px;width:100%;height:100%;
  opacity: .5;
  background: #ff3344;
}

.cannot_edit .qq-upload-drop-area {
  display: none !important;
}

.qq-total-progress-bar-container-selector.qq-total-progress-bar-container {
    position: absolute;
    bottom: -2px;
    left: 2px;
    width: 100%;
    height: 4px;
}

.qq-progress-bar {
    width: 20%;
    height: 4px;
    background: #ff3344;
}

.qq-hide {
  display:none;
}
